import { Row, Col } from 'react-bootstrap';

import { CustomModal } from '../default';

type Props = {
    show: boolean,
    handleClose: () => void
}

export default function AdBlockDialog({ show, handleClose }: Props) {
    return (
        <CustomModal show={show} handleClose={handleClose} title="PERA A! " style={{ backgroundColor: '#692c2c' }}>
            <Row>
                <Col style={{ textAlign: 'center' }}>
                    <p>Percebi que voc est usando um bloqueador de anncios!</p>
                    <p>Adicione o <strong>verbio.site</strong> como exceo em sua extenso e apoie nosso projeto vendo os anncios!</p>
                    <p>Ajuda nis... </p>
                </Col>
            </Row>
        </CustomModal>
    )
}
