import { Row, Col } from 'react-bootstrap';

import { CustomModal } from '../default';

import { GuessBlock } from '../game';

import { NUMBER_OF_LETTERS_OF_WORD, exampleGuesses } from '../../config'

type Props = {
    show: boolean,
    handleClose: () => void
}

export default function HelpDialog({ show, handleClose }: Props) {
    return (
        <CustomModal show={show} handleClose={handleClose} title="Como jogar">
            <Row>
                <Col style={{ textAlign: 'center' }}>
                    <p>Todos os dias um novo verbo surge para voc advinhar em at 3 tentativas.</p>
                    <p>Verbo  uma palavra que indica ao. No jogo, os verbos estaro no infinitivo. <strong>Exemplo: pegar, amarrar, etc.</strong></p>
                    <p>Cada tentativa deve ser uma palavra vlida de {NUMBER_OF_LETTERS_OF_WORD} letras. Acentos e cedilha so desconsiderados.</p>
                    <p>Ao efetuar uma tentativa, a cor das letras ir indicar se voc est perto da resposta ou no.</p>
                    <p>Uma dica (sinnimos da resposta) ir aparecer a partir da segunda tentativa.</p>
                    <p><strong>Apenas verbos so aceitos nas tentativas</strong>.</p>
                </Col>
            </Row>
            <hr />
            <Row>
                <Col style={{ textAlign: 'center' }}>
                    <div>
                        <p>A letra verde indica que ela est presente na palavra e a posio est correta.</p>
                        <GuessBlock current={false} placeholder={false} guess={exampleGuesses.correct} />
                    </div>
                    <div style={{ marginTop: '15px' }}>
                        <p>A letra amarela indica que ela est presente na palavra porm em outra posio.</p>
                        <GuessBlock current={false} placeholder={false} guess={exampleGuesses.displaced} />
                    </div>
                    <div style={{ marginTop: '15px' }}>
                        <p>A letra cinza indica que ela no est na palavra.</p>
                        <GuessBlock current={false} placeholder={false} guess={exampleGuesses.wrong} />
                    </div>
                </Col>
            </Row>
            <hr />
            <Row>
                <Col style={{ textAlign: 'center' }}>
                    Criado por <a href="https://twitter.com/jvdutrag">@jvdutrag</a>
                </Col>
            </Row>
        </CustomModal>
    )
}
